.statistics {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 300px;
  margin: 0 auto;
  border-radius: 5px;
}

.title {
  padding-top: 10px;
  padding-bottom: 10px;
  font-size: 25px;
  text-shadow: 0px 0px 15px rgb(255, 87, 191);
  color: #ffffff;
}

.statList {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  padding: 0;
  margin-bottom: 10px;
}

.item {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: calc(100% / 5);
  color: #ffffff;
  cursor: pointer;
}
